import styled from 'styled-components';

export const Wrapper = styled.div`
    .video-preview {
        width: 284px;
        height: 556px;
        position: relative;

        .video {
            height: 486px;
            width: 259px;
            background-color: rgb(0, 0, 0);
            object-fit: contain;
            margin-left: 12px;
            margin-top: 13px;
            border-radius: 0.5px;
        }

        .title {
            position: absolute;
            top: 40px;
            left: 0;
            right: 0;
            z-index: 99;
            color: white;
            text-align: center;
            font-size: 1.2rem;
            opacity: 0.6;

            span {
                margin-right: 20px;
                color: rgb(255 255 255 / 75%);
            }
        }

        .meta-data {
            position: absolute;
            bottom: 80px;
            left: 25px;
            z-index: 2;
            opacity: 0.6;
            color: white;
            font-size: 1.2rem;

            .username {
                margin-bottom: 4px;
            }
            .caption {
                margin-bottom: 4px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: normal;
            }

            .sound-container {
                display: flex;
                align-items: center;

                .sound {
                    margin-left: 6px;
                    max-width: 150px;
                    overflow: hidden;
                    white-space: nowrap;
                }
            }
        }
        .avatar-container {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 20px;

            .avatar {
                width: 40px;
                height: 40px;
                border-radius: 50%;
                opacity: 0.6;
                object-fit: contain;
            }
        }
        .album-container {
            position: absolute;
            bottom: 74px;
            right: 20px;
            opacity: 0.6;

            .album {
                width: 40px;
                height: 40px;
                border-radius: 50%;
                background: conic-gradient(
                    from 90deg at 50% 50%,
                    rgb(57, 57, 57) -40.11deg,
                    rgb(21, 21, 21) 47.27deg,
                    rgb(57, 57, 57) 143.02deg,
                    rgb(22, 22, 22) 227.49deg,
                    rgb(57, 57, 57) 319.89deg,
                    rgb(21, 21, 21) 407.27deg
                );
            }

            .album-img {
                width: 24px;
                height: 24px;
                border-radius: 50%;
                object-fit: contain;
                position: absolute;
                top: 8px;
                right: 8px;
                animation: rotate 2s linear infinite;
            }
        }
        .music-bar-icon {
            position: absolute;
            top: 50%;
            -webkit-transform: translateY(-40%);
            -ms-transform: translateY(-40%);
            transform: translateY(20%);
            right: 25px;
            opacity: 0.6;
        }

        .tiktok-app-frame {
            background-image: url(data:image/png;base64,);
            background-size: cover;
            height: 100%;
            width: 100%;
            min-width: 100%;
            min-height: 100%;
            position: absolute;
            top: 0px;
            left: 0px;
            z-index: 1;
        }
        .video-controls {
            position: absolute;
            width: 100%;
            height: 100%;
            z-index: 3;
            top: 0px;
            overflow: hidden;

            .video-controls-bottom {
                position: absolute;
                bottom: 5px;
                width: calc(100% - 10px);
                margin-left: 5px;
                margin-right: 5px;
                height: 90px;
                transition: all 0.3s ease-out 0s;
                background: linear-gradient(
                    rgba(0, 0, 0, 0.15) 0%,
                    rgba(0, 0, 0, 0.65) 100%
                );
                border-radius: 8px 8px 20px 20px;
                opacity: 0;

                .detail {
                    position: absolute;
                    height: 20px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    width: calc(100% - 45px);
                    transform: translateX(-50%);
                    left: 50%;
                    bottom: 50px;

                    .left {
                        display: flex;
                        align-items: center;
                        height: 100%;

                        .play {
                            line-height: 0;
                            cursor: pointer;
                            img {
                                width: 1.6rem;
                                height: 1.6rem;
                            }
                        }
                        .time {
                            color: #fff;
                            padding: 0px 6px;
                            margin-top: 2px;
                            font-size: 1.4rem;
                        }
                    }

                    .right {
                        display: flex;
                        align-items: center;
                        .volume {
                            margin-right: 14px;
                        }
                        .fullscreen,
                        .volume {
                            line-height: 0;
                            cursor: pointer;
                            img {
                                width: 1.6rem;
                                height: 1.6rem;
                            }
                        }
                    }
                }
            }

            &:hover > .video-controls-bottom {
                opacity: 1;
                color: red;
            }
        }

        .progress-wrap {
            position: absolute;
            bottom: 20px;
            width: calc(100% - 40px);
            left: 50%;
            transform: translateX(-50%);
            cursor: pointer;

            .progress {
                width: 100%;
                height: 3px;
                background-color: rgb(255 255 255 / 20%);
                position: absolute;
                z-index: 2;
            }

            .circle {
                position: absolute;
                width: 5px;
                height: 5px;
                border-radius: 50%;
                top: -1px;
                background-color: #fff;
                box-shadow: rgb(0 0 0 / 10%) -1px 1px 1px;
            }

            .bar {
                position: absolute;
                z-index: 1;
                background-color: #fff;
                height: 3px;
            }
        }
    }
`;
